/* Header NavBar section */
header {
	background-color: white;
	min-height: 100px;
	min-width: 100%;
	display: flex;
	justify-content: space-between;
	font-size: 22px;
}

header .selected {
  text-decoration: none !important;
  list-style: none !important;
  color: black ;
}

.item .active.selected {
  border-bottom: 4px solid #b84e4e;
}


.App {
	margin: 0;
}

body {
	font-family: "Dosis", sans-serif;
}

main {
	overflow: hidden;
}

nav {
  justify-content: center;
  align-items: center;
  background-color: transparent !important;
}

 ul li a:hover {
  color: #b84e4e;
}


nav ul {
	margin-top: 20px;
}

nav ul li {
	list-style: none;
	display: inline-block;
	padding: 20px;
}

nav ul li a {
  text-decoration: none;
  display: block;
  font-size: 24px;
  text-transform: capitalize;
  font-weight: 400;
  color: #b84e4e;
  padding: 0 10px;
  font-weight: 600;
}

.logo h3 {
	font-family: "Dancing Script";
	font-size: 42px;
	margin-top: 30px;
	margin-left: 40px;
}

.logo {
	color: black !important;
}

.spot {
	margin-top: 100px;
}


.search{
  padding: 0 10px; 
  display: flex; 

}

.search form {
  display: flex;
  margin: 40px 100px 40px 0;

}

.searchInput {
	border: 0;
	width: 0;
	transition: width 0.1s ease-in;
}

.searchInput.active {
	width: 150px;
}

.searchInput:focus {
  outline: none;
  color: #b84e4e; 
}

.searchIcon {
  cursor: pointer;
  width: 200px;
  margin: 40px; 
  
} 


/* Header NavBar section ends */

/* Footer */

footer {
	background: white;
	height: 65px;
	width: 100%;
	display: flex;
	justify-content: center;
	align-items: center;
	margin: 100px 0 0 210px;
}

footer ul li {
	list-style: none;
	display: inline-block;
	padding: 20px;
}

footer ul li a {
  text-decoration: none;
  display: block;
  font-size: 22px;
  text-transform: capitalize;
  font-weight: 400;
  color: #000; 
  padding: 0 10px;
  font-weight: 600;
}

/* Footer ends */

/* Spot css starts*/
.carousel img {
	width: 400px;
	object-fit: cover;
	object-position: center;
}
.carousel {
	width: 100%;
	height: 500px;
	overflow: hidden;
}

h2 {
	text-align: center;
	letter-spacing: 0;
	line-height: 1.3em;
	text-transform: capitalize;
	font-size: 50px;
	margin: 0;
	margin-bottom: 20px;
	font-weight: 400;
	font-family: "Dosis", sans-serif;
}

/*Spot css ends*/
.allspots {
	width: 50%;
	margin: auto;
}
.allspots {
	text-align: center;
	border: 2px solid #ddd;
	border-radius: 5px;
	padding: 0px 40px 0px 40px;
	background-color: rgb(252, 252, 252);
}

.spots {
	font-family: "Dosis", sans-serif;
}
.tag {
	background-color: #e07b5c;
	color: white;
	padding: 5px;
	font-size: 20px;
	border-radius: 10px;
}

.btn-flat {
	background-color: #e07b5c;
	color: white;
	margin-bottom: 10px;
}
.btn-xxl {
	font-size: 35px;
}
/*Spot css ends*/
